body {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

h1 {
  text-align: center;
  color: #333;
  margin-bottom: 20px;
}

.controls {
  margin-bottom: 20px;
}

.controls label {
  margin-bottom: 10px;
  font-size: 16px;
  display: block;
}

.controls input[type="range"] {
  width: 100%;
}

.controls p {
  margin: 10px 0;
  font-size: 18px;
}

.stats {
  text-align: center;
  margin-bottom: 20px;
}

.modal-overlay {
  background: #00000080;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.modal {
  background-color: #fff;
  border-radius: 8px;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  display: flex;
  box-shadow: 0 0 10px #0000004d;
}

.modal h2 {
  margin-bottom: 20px;
}

.modal button {
  cursor: pointer;
  color: #fff;
  border: none;
  border-radius: 4px;
  margin: 5px;
  padding: 10px 20px;
  font-size: 16px;
  transition: background-color .3s;
}

.modal button:hover {
  opacity: .8;
}

.modal button:first-of-type {
  background-color: orange;
}

.modal button:nth-of-type(2) {
  background-color: green;
}

.modal button:nth-of-type(3) {
  background-color: red;
}

.modal button:nth-of-type(4) {
  background-color: #00f;
}
